:root {
  --gassed-green: #00ff7a;
  --gassed-purple: #7a00ff;
  --gassed-orange: #ff7a00;
}

.checkout-div {
  z-index: 1;
  background-image: url("streets.eb6097ed.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100vw;
  min-height: 100vh;
  padding: 20px 0;
  position: relative;
}

.checkout-div:before {
  content: "";
  z-index: 2;
  background-color: #000c;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.checkout-container {
  margin: 20px;
}

.checkout-product-div {
  justify-content: center;
  align-items: left;
  z-index: 3;
  flex-wrap: wrap;
  display: flex;
  position: relative;
}

#price-quantity-checkout {
  flex-direction: row;
  justify-content: space-between;
  margin-top: 10px;
  display: flex;
}

#checkout-products {
  text-align: center;
  z-index: 3;
  border: 3px solid #fcfcfc78;
  width: 50%;
  margin: 30px auto;
  padding: 30px 30px 20px;
  font-weight: 500;
  position: relative;
}

.empty-div {
  color: #fff;
  text-align: center;
  z-index: 3;
  background-color: #38383877;
  width: 60%;
  height: 50%;
  margin: 30px auto;
  padding: 30px;
  font-size: 25px;
  position: relative;
}

.empty-text {
  margin-top: 30px;
  margin-bottom: 20px;
  font-family: Paytone One, sans-serif;
  font-size: 30px;
}

